.header {
    padding-top: 20px;
    position: relative;
    width: 100%;
    overflow: hidden;
    background-image: url('http://offers.premierinc.com/rs/381-NBB-525/images/17123_dark-bg-img.jpg');
    background-size: cover;
    background-position: center;
    .logo {
        position: relative;
        top: 0;
        left: 0;
        img {
            max-width: 200px;
            display: inline-block;
            vertical-align: middle;
            @media only screen and (max-width: 600px) {
                display: block;
                margin: 0 auto;
                margin-bottom: 2rem;
            }
        }
        .divider {
            width: 1px;
            height: 75px;
            display: inline-block;
            background-color: #fff;
            vertical-align: middle;
            margin: 0 20px;
            @media only screen and (max-width: 600px) {
                display: none;
            }
        }
        h1 {
            display: inline-block;
            vertical-align: middle;
            color: #ffffff;
            font-weight: normal;
            @media only screen and (max-width: 600px) {
                display: block;
                text-align: center;
            }
        }
    }
    .learn {
        position: fixed;
        top: 20px;
        right: 0;
        z-index: 999;
        a {
            color: #ffffff;
            text-decoration: none;
            display: block;
            padding: 20px 30px;
            background-color: $orange;
            transition: all ease-out .25s;
            max-width: 300px;
            margin-bottom: 20px;
            box-shadow: 0 0 10px 5px rgba(0,0,0,0.25);
            p {
                color: #ffffff;
                margin-bottom: 0;
                line-height: 1;
            }
            .read_it {
                font-size: 1.75rem;
                text-transform: uppercase;
                text-align: center;
            }
            .read_it_sub {
                font-size: .55rem;
                text-align: center;
            }
            .contact_expert {
                font-size: 1.15rem;
                text-transform: uppercase;
                text-align: center;
            }
            .contact_expert_sub {
                font-size: .55rem;
                text-align: center;
            }
            &:hover {
                background-color: darken($orange, 10%);
            }
            &:last-of-type {
                background-color: $blue;
                &:hover {
                    background-color: darken($blue, 10%);
                }
            }
        }
        @media only screen and (max-width: 900px) {
            top: 0;
            left: 0;
            width: 100%;
            z-index: 888;
            a {
                display: block;
                width: 50%;
                margin: 0;
                max-width: 1000px;
                height: 100px;
                box-shadow: 0;
                &:first-of-type {
                    float: left;
                }
                &:last-of-type {
                    float: right;
                }
            }
            &:after {
                display: block;
                content: '';
                clear:both;
            }
        }
        @media only screen and (max-width: 600px) {
            a {
                width: 100%;
                float: none;
                min-height: 50px;
                height: auto;
            }
        }
    }
    .text_block {
        width: 60%;
        margin-bottom: 3rem;
        text-align: left;
        margin-top: 100px;
        @media only screen and (max-width: 900px) {
            width: 100%;
        }
        p, h1, h2 {
            color: #ffffff;
        }
        h1 {
            font-weight: 900;
            letter-spacing: 1px;
        }
    }
}

.mobile_button_padding {
    width: 100%;
    height: 120px;
}